import React, {useState, useEffect} from 'react';
import connect from '@vkontakte/vkui-connect-promise';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

const fetchedUser = async (setUser) => {
	const response = await connect.send('VKWebAppGetUserInfo', {});
	const data = await response.data;
	setUser(data);
};

const fetchedAccesToken = async (setAccessToken) => {
	const response = await connect.send("VKWebAppGetAuthToken", {"app_id": 6940370, "scope": "friends,status"});
	const data = await response;
	console.log(data);
	setAccessToken(data);
};

const App = (props) => {
	const [user, setUser] = useState({
		photo_200: '',
		first_name: 'Вася',
		last_name: 'Васечкин',
		city: { title: 'New York' }
	});
	const [activePanel, setActivePanel] = useState('home');
	const [accessToken, setAccessToken] = useState('');

	const go = (e) => setActivePanel(e.currentTarget.dataset.to);

	useEffect(() => {
		fetchedUser(setUser);
		fetchedAccesToken(setAccessToken);
	}, []);

	return (
		<View activePanel={activePanel}>
			<Home id="home" fetchedUser={user} go={go} />
			<Persik id="persik" go={go} />
		</View>
	);
};

export default App;